.container {
	width: 100%;
 max-width: 1240px;
 margin: 0 auto;
 padding: 0 2rem;
}

.post-enter {
 -webkit-transform: translateX(-350px);
         transform: translateX(-350px);
}
.post-enter-active {
 -webkit-transform: translateX(0px);
         transform: translateX(0px);
 transition: all 500ms ease-in;
}
.post-exit {
 opacity: 1;
}
.post-exit-active {
 -webkit-transform: translateX(-350px);
         transform: translateX(-350px);
 transition: all 500ms ease-in;
 opacity: 0.1;
}

.page__wrapper {
 margin-top: 5%;
 margin-bottom: 5%;
 display: flex;
	justify-content: center;
}

.page {
	padding: 0 1rem;
	background-color: var(--color-ui-base);
	box-shadow: var(--shadow);
	line-height: 2.5;
	border-radius: 0.5rem;
	border-radius: var(--radius);
	border: none;
	align-items: center;
	grid-gap: 0.75rem;
	gap: 0.75rem;
	color: var(--color-text);
	cursor: pointer;
	margin: 5px;
}

.page__current {
	font-weight: bold;
	border: 1px solid;
}


